body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', 'Arial', sans-serif;
  background-color: #fff; }

.container {
  padding-right: 30px;
  padding-left: 30px;
  margin: 0 auto; }

img {
  max-width: 100%;
  height: auto; }

.button {
  border: 2px solid #444444;
  color: #444444;
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
  text-decoration: none; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

