.page-header__wrapper {
  width: 320px;
  display: flex; }

.page-header__logo {
  height: 79px; }

.page-header__main-nav {
  width: 320px; }

.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  font-size: 17px;
  line-height: 24px;
  font-weight: 700; }

.cite-list__item, .user-list__item {
  margin: 0;
  padding: 0;
  border-top: 1px solid #ececec;
  padding-top: 27px;
  padding-bottom: 27px;
  padding-left: 75px; }

.cite-list__link {
  margin: 0;
  padding: 0;
  text-decoration: none;
  color: #000; }

.sub-cite-list__wrapper {
  display: none; }

.preview {
  width: 320px; }

.preview__slogan {
  display: block;
  padding: 72px 36px 120px 36px;
  background: #fff url("../img/hero-mobile.jpg") no-repeat;
  background-size: cover;
  font-size: 27px;
  line-height: 30px;
  font-weight: 700;
  text-align: center; }

.preview__list {
  margin: 0;
  padding: 0;
  list-style: none; }

.preview__item {
  padding: 28px 120px 30px 28px;
  font-size: 19px;
  line-height: 24px;
  font-weight: 700;
  color: #fff; }
  .preview__item--interior {
    background-color: #63d1bb; }
  .preview__item--toys {
    background-color: #56c3b2; }

.top-item {
  position: relative;
  width: 260px; }

.top-item__title {
  margin: 0;
  padding: 0;
  font-size: 17px;
  line-height: 20px;
  font-weight: 700;
  text-transform: uppercase;
  color: #63d1bb;
  position: absolute;
  top: 63px; }

.top-item__subtitle {
  margin: 0;
  padding: 0;
  font-size: 25px;
  line-height: 30px;
  font-weight: 700;
  padding-top: 30px;
  padding-bottom: 50px; }

.top-item__info {
  color: #444444;
  font-size: 15px;
  line-height: 24px;
  font-weight: 400; }

.top-item__descriptions {
  color: #444444;
  font-size: 15px;
  line-height: 24px;
  font-weight: 400; }

.top-item__price {
  color: #444444;
  font-size: 17px;
  line-height: 24px;
  font-weight: 700; }

.top-item__order {
  border: 2px solid #444444;
  color: #444444;
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
  text-decoration: none; }

.features {
  width: 260px; }

.features__title {
  font-size: 25px;
  line-height: 30px;
  font-weight: 700; }

.features__list {
  margin: 0;
  padding: 0;
  margin-bottom: 50px;
  list-style: none; }

.features__item {
  padding-top: 85px;
  font-size: 17px;
  line-height: 20px;
  font-weight: 700; }

.reviews {
  width: 260px; }

.reviews__title {
  font-size: 25px;
  line-height: 30px;
  font-weight: 700; }

.reviews__item {
  margin: 0;
  padding: 0; }

.reviews-text {
  margin: 0;
  padding: 0;
  font-size: 15px;
  line-height: 30px;
  font-weight: 400;
  color: #444444; }

.reviews__author {
  font-size: 17px;
  line-height: 24px;
  font-weight: 700; }

.reviews__adress {
  font-size: 17px;
  line-height: 30px;
  font-weight: 400;
  color: #444444; }

.reviews__button {
  padding-top: 0px; }

.contacts {
  width: 260px; }

.contacts__title {
  font-size: 25px;
  line-height: 30px;
  font-weight: 700; }

.contacts__list {
  margin: 0;
  padding: 0;
  font-size: 15px;
  line-height: 30px;
  font-weight: 400;
  color: #444444; }

.contacts__adress {
  margin: 0;
  padding: 0;
  padding-right: 20px;
  white-space: nowrap; }

.contacts__value {
  margin: 0;
  padding: 0; }

.contacts__mail {
  display: block; }

.copyright__text {
  font-size: 18px;
  line-height: 19px;
  font-weight: 400; }
